.newsMain {
  width: 100%;
  height: 130vh;
  display: flex;
  flex-direction: column;
  padding-top: 1rem;
  padding-bottom: 2rem;

  h1 {
    font-size: $size-5;
    color: $white-text;
  }

  .newsMain__content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    align-content: center;

    .content_row {
      width: 30%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-right: 5px;

      .content_row__item {
        width: 100%;
        height: 30%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: start;
        margin: 1rem;
        background-color: $background-elements;
        border-radius: 1rem;
        padding: 1rem;
        cursor: pointer;
        transition: all 0.3s ease-in-out;

        &:hover {
          background-color: $background-elements-head;
        }

        h2 {
          font-size: $size-4;
          color: $white-text;
          font-weight: $font-weight-3;
          text-align: center;
        }

        h3 {
          font-size: $size-3;
          color: $purple-components;
          font-weight: $font-weight-2;
          text-align: center;
          padding-left: 5px;
          border-left: 2px solid $purple-components;
        }

        p {
          font-size: $size-1;
          color: $white-text;
          font-weight: $font-weight-1;
        }

        img {
          width: 100%;
          height: 60%;
          border-radius: 1rem;
          object-fit: cover;
          border-radius: 1rem;
        }
      }

      .content_row__item:first-child {
        height: 45%;
      }
    }

    .content_row_2 {
      width: 30%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin: 5px;

      .content_row__item {
        width: 100%;
        height: 30%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: start;
        margin: 1rem;
        background-color: $background-elements;
        border-radius: 1rem;
        padding: 1rem;
        cursor: pointer;
        transition: all 0.3s ease-in-out;

        &:hover {
          background-color: $background-elements-head;
        }

        h2 {
          font-size: $size-4;
          color: $white-text;
          font-weight: $font-weight-3;
          text-align: center;
        }

        h3 {
          font-size: $size-3;
          color: $purple-components;
          font-weight: $font-weight-2;
          text-align: center;
          padding-left: 5px;
          border-left: 2px solid $purple-components;
        }

        p {
          font-size: $size-1;
          color: $white-text;
          font-weight: $font-weight-1;
        }

        img {
          width: 100%;
          height: 70%;
          border-radius: 1rem;
          object-fit: cover;
          border-radius: 1rem;
        }
      }

      .content_row__item:nth-child(2) {
        height: 45%;
      }
    }

    .content_row_3 {
      width: 30%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin: 5px;

      .content_row__item {
        width: 100%;
        height: 30%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: start;
        margin: 1rem;
        background-color: $background-elements;
        border-radius: 1rem;
        padding: 1rem;
        cursor: pointer;
        transition: all 0.3s ease-in-out;

        &:hover {
          background-color: $background-elements-head;
        }

        h2 {
          font-size: $size-4;
          color: $white-text;
          font-weight: $font-weight-3;
          text-align: center;
        }

        h3 {
          font-size: $size-3;
          color: $purple-components;
          font-weight: $font-weight-2;
          text-align: center;
          padding-left: 5px;
          border-left: 2px solid $purple-components;
        }

        p {
          color: $white-text-70;
        }

        img {
          width: 100%;
          height: 70%;
          border-radius: 1rem;
          object-fit: cover;
          border-radius: 1rem;
        }
      }

      .content_row__item:nth-child(3) {
        height: 45%;
      }
    }
  }
}
