* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", sans-serif;
  // background-color: #101924;
}

.wrapper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.wrapperBreak {
  width: 100%;
  margin: 0 auto;
  max-width: 1200px;
  height: 10vh;
  border-bottom: 1px solid #545b63;
}

.dividingBreak {
  width: 100%;
  margin: 0 auto;
  max-width: 1200px;
  height: 20vh;
}

body {
  background-color: #101924;
}
