.categoriesGamesContainer {
//   height: 150vh;
//   min-height: 200vh;
  display: flex;
  flex-direction: row;

  .leftCategories {
    width: 25%;
    height: 85%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1 {
      font-size: 2rem;
      color: $white-text;
      margin: 0;
      padding: 20px 0;
    }

    .menuCategories {
      height: 300px;
      width: 100%;
      background-color: $background-elements;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      border-radius: 10px;
      padding: 1rem;

      ul {
        list-style: none;
        padding: 0;
        margin: 0;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        li {
          width: 100%;
          height: 15%;
          display: flex;
          flex-direction: column;
          font-size: $size-3;
          color: $white-text-70;
          cursor: pointer;
          transition: all 0.3s ease-in-out;

          &:hover {
            color: $white-text;
          }
        }
      }
    }

    .leftEl {
      width: 100%;
      height: 350px;
      background-color: $background-elements;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      border-radius: 10px;
      margin-top: 30px;

      img {
        width: 100%;
        height: 60%;
        object-fit: cover;
        border-radius: 10px 10px 0 0;
      }

      .leftEl__info {
        width: 100%;
        height: 40%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;

        h3 {
          font-size: $size-3;
          color: $white-text;
          margin: 0;
        }

        p {
          font-size: $size-1;
          color: $white-text-70;
          margin: 0;
        }
      }
    }
  }

  .rightCategories {
    width: 75%;
    height: 100%;
    display: flex;
    flex-direction: column;
    // justify-content: space-between;

    .headRightCategories {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 0 1rem;

      h1 {
        font-size: 2rem;
        color: $white-text;
        margin: 0;
        padding: 20px 0;
      }

      .filters {
        width: 10%;
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        p {
          cursor: pointer;
          font-size: $size-1;
          color: $white-text-70;
          margin: 0;
          padding: 0 1rem;
          transition: all 0.3s ease-in-out;
        }
      }
    }

    .gameListItems {
      width: 100%;
      height: 90%;
      display: flex;
      flex-direction: column;
      border-radius: 5px;
      margin-top: 1.5rem;

      .gameListItem {
        width: 100%;
        height: 10%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        margin: 1rem;
        cursor: pointer;
        background-color: $background-elements;
        transition: all 0.3s ease-in-out;
        color: $white-text-70;
        font-size: $size-2;
        border-radius: 5px;

        &:hover {
          color: $white-text;
        }

        img {
          width: 30%;
          height: 100%;
          border-radius: 5px;
        }

        .gameListItemInfo {
          width: 70%;
          height: 100%;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
          padding: 0 1rem 0 1rem;

          .infoItemLeft {
            width: 70%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-start;
          }

          .itemInfoRight {
            width: 30%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            .spanInfo {
              color: $gray-text;
              font-size: $size-0;
            }
          }
        }
      }
    }
  }
}
